import React, { Component } from 'react';
import { connect } from 'react-redux';

import { requestApps } from '../actions';
import { Card } from 'mimir-ui';

class AppGrid extends Component {

    componentDidMount() {
        this.props.getApps();
    }

    renderApps = () => {
        return  (
            this.props.apps.map(app => (
                <Card onClick={"window.location.href='/reporting'"}>
                    <Card.Image src={ app.banner } />
                    <Card.Content>
                        <Card.Header>
                            { app.name }
                        </Card.Header>
                        <Card.Description>
                            { app.description }
                        </Card.Description>
                    </Card.Content>
                </Card>
            ))
        );
    }

    render() {
        return this.renderApps();
    }
}

export default connect(
    state => ({
        apps: state.app.apps,
    }),
    dispatch => ({
        getApps() {
            dispatch(requestApps());
        },
    })
)(AppGrid);
