import * as types from 'actionTypes';
import * as actions from 'actions';

import { concatMap, map } from 'rxjs/operators';
import { ofType } from 'redux-observable';
import { ajax } from 'rxjs/ajax';

export const requestApps = action$ => action$.pipe(
    ofType(types.REQUEST_APPS),
    concatMap(action =>
        ajax.getJSON(`/core/apps`).pipe(
            map(response => actions.receiveApps(response))
        )
    )
);

export default [
    requestApps,
];