import * as actions from '../actionTypes';
import update from 'immutability-helper';

const initialState = {
    apps: [],
};

const file = (state = initialState, action) => {
    switch (action.type) {
        case actions.RECEIVE_APPS: {
            return update(state, {
                apps: { $push: action.payload.apps }
            });
        }

        default:
            return state;
    }
};

export default file;