import * as action from 'actionTypes';

export const actionCreator = type => {
    return (payload={}, meta={}, error={}) => ({
            type,
            payload,
            meta,
            error,
    });
}

export const requestApps = () => ({
    type: action.REQUEST_APPS,
});

export const receiveApps = response => {
    return actionCreator(action.RECEIVE_APPS)({
        apps: response || [],
    });
};

export const rejectApps = error => {
    return actionCreator(action.REJECT_APPS)({
        error,
    });
};